export default "@import '../../themes/src/default.css';\n\n.gap {\n    flex-shrink: 0;\n}\n\n.vertical {\n    overflow: hidden;\n    display: block;\n}\n\n.horizontal {\n    display: inline-block;\n    overflow: hidden;\n}\n\n@each $size in 3xs, 2xs, xs, s, m, l, xl, 2xl, 3xl, 4xl, 5xl, 6xl, 7xl, 8xl {\n    .vertical.gap[data-gap-size='$(size)'] {\n        height: var(--gap-$(size));\n    }\n}\n\n@each $size in 3xs, 2xs, xs, s, m, l, xl, 2xl, 3xl, 4xl, 5xl, 6xl, 7xl, 8xl {\n    .horizontal.gap[data-gap-size='$(size)'] {\n        width: var(--gap-$(size));\n    }\n}\n";